import React, { Component } from 'react';
import MHeader from '@/components/MHeader';
import { connect } from 'react-redux';
import * as actions from '@/redux/actions/userInfo'
import LoginComponent from '@/components/LoginComponent'

class Login extends Component {
    constructor() {
        super();
        this.state = {
            login: true
        }
    }
    render() {       
        return (
            <div style={{paddingTop: 48}}>
              <MHeader title=''/>
              {/*    */}
              {this.state.login? <LoginComponent login={this.login}/> : null}
            </div>
        )
    }
    login = (username) => {
       this.props.commitLoginInfo(username); // 
    //    this.props.history.push('/user'); // user
       let backPath = this.props.match.params.router;
       if(backPath){
           this.props.history.push(decodeURIComponent(backPath))
       } else {
           this.props.history.push('/user'); // 
       }
    }
    checkLogin = () => { // 
        if(this.props.userInfo.username) {
            return this.props.history.push('/user');
        }
        this.setState({login: true});
    }
    componentWillMount() { // 
        this.checkLogin();
    }
}

export default connect(
    state => ({...state}),
    actions
)(Login)


