import * as Types from '../action-types';

// city action
export const updateCity = (data) => (
    {
        type: Types.UPDATE_CITY,
        data
    }
);

// 
export const getShopList = (data) => (
    {
        type: Types.GET_SHOP_LIST,
        data
    }
);


// 
export const commitLoginInfo = username =>(
    {
        type: Types.LOGIN_USER_INFO,
        username
    }
)


